import React, { useState } from "react";

function Index({ message }) {
    const [state, setState] = useState(0);

    function cliked() {
        setState(state + 1);
    }

    return (
        <div className="container">
            <h1>Hello </h1>
            <p>{state}</p>
            <button onClick={cliked} className="btn btn-primary">
                Add
            </button>
            <h2>{message}</h2>
        </div>
    );
}

export default Index;
